<template>
  <div class="1box 1bg-white">
    <div class="table-container">
      <table v-if="response?.datas?.length">
        <thead>
          <tr>
            <th>測量日期</th>
            <th>訓練前體重(g)</th>
            <th>訓練後體重(g)</th>
            <th>脫水率</th>
            <th>飲水量(ml)</th>
            <th>排尿量(ml)</th>
            <th>總流失量(ml)</th>
            <th>訓練時間(min)</th>
            <th>流汗率(ml/min)</th>
            <th>可容許脫水率
              <input type="number" class="dehydration-input" v-model.number="可容許脫水率" min="0" max="100" step="0.1"
                @blur="驗證可容許脫水率" />
            </th>
            <th>每小時建議飲水量(ml)</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in 排序後的數據" :key="item.date">
            <td>{{ item.date }}</td>
            <td>{{ item.訓練前體重 }}</td>
            <td>{{ item.訓練後體重 }}</td>
            <td>{{ item.脫水率 }}%</td>
            <td>{{ item.飲水量 }}</td>
            <td>{{ item.排尿量 }}</td>
            <td>{{ item.總流失量 }}</td>
            <td>{{ item.訓練時間 }}</td>
            <td>{{ item.流汗率.toFixed(2) }}</td>
            <td>{{ 可容許脫水率 }}</td>
            <td>{{ 計算每小時建議飲水量(item).toFixed(2) }}</td>
          </tr>
        </tbody>
      </table>

      <p v-else class="no-data">暫無數據</p>
    </div>
  </div>
</template>

<script setup>

const response = inject('response')
const 可容許脫水率 = inject('可容許脫水率')


const 驗證可容許脫水率 = () => {
  if (可容許脫水率.value < 0 || 可容許脫水率.value > 100) {
    可容許脫水率.value = Math.max(0, Math.min(100, 可容許脫水率.value))
  } 
}

// 數據按測量日期排序（最新在前）
const 排序後的數據 = computed(() => {
  if (!response.value?.datas?.length) return []
  return [...response.value.datas].sort((a, b) =>
    new Date(b.date) - new Date(a.date)
  )
})

// 公式: 流汗率 * 訓練時間 - (訓練前體重 * 可容許脫水率 / 100)
const 計算每小時建議飲水量 = (item) => {
  return item.流汗率 * item.訓練時間 - (item.訓練前體重 * 可容許脫水率.value / 100)
}
</script>

<style scoped>
.dehydration-input {
  display: flex;
  align-items: center;
  padding: 4px 12px;
  border-radius: 4px;
  border: 1px solid #ccc;
  background-color:white;
  width: 60px;
  font-size: 14px;
}

.table-container {
  overflow-x: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 16px;
  font-size: 13px;
}

th,
td {
  border: 1px solid #ddd;
  padding: 10px 8px;
  text-align: center;
  white-space: nowrap;
}

th {
  background-color: #f5f5f5;
  font-weight: bold;
  position: sticky;
  top: 0;
  z-index: 1;
}

tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

tbody tr:hover {
  background-color: #e3f2fd;
}

.no-data {
  text-align: center;
  padding: 40px;
  color: #999;
  font-size: 16px;
}
</style>