<template>
  <div ref="chartContainer" style="height: 300px;min-width: 600px;">
    <p v-if="!response?.datas?.length">暫無數據</p>
  </div>
</template>

<script setup>
import * as echarts from 'echarts'

const response = inject('response')

const chartContainer = ref(null)
let chartInstance = null

const resizeHandler = () => {
  chartInstance?.resize()
}

const initChart = () => {
  if (!chartContainer.value || !response.value?.datas?.length) return

  if (!chartInstance) {
    chartInstance = echarts.init(chartContainer.value)
  }

  const datas = response.value.datas

  const dates = datas.map(item => item.date)
  const waterIntake = datas.map(item => item.飲水量)
  const achievementRate = datas.map(item => {
    const trainingHours = item.訓練時間 / 60 // 分钟转小时
    const recommendedIntake = item.每小時建議飲水量 || 0
    const actualIntake = item.飲水量 || 1
    // 確保回傳數字（而非字串）
    return Number(((trainingHours * recommendedIntake) / actualIntake * 100).toFixed(2))
  })

  const option = {
    color: CHART_COLORS,
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'cross'
      }
    },
    // 把 legend（系列標籤 / series name）放到畫面上方並置中
    legend: {
      data: ['飲水量', '喝水達成率'],
      top: 'top',
      left: 'center'
    },
    xAxis: {
      type: 'category',
      data: dates,
      axisLabel: {
        rotate: 45
      }
    },
    yAxis: [
      {
        type: 'value',
        name: '飲水量 (ml)',
        position: 'left'
      },
      {
        type: 'value',
        name: '喝水達成率 (%)',
        position: 'right'
      }
    ],
    series: [
      {
        name: '飲水量',
        type: 'bar',
        yAxisIndex: 0,
        data: waterIntake,
        itemStyle: {
          color: CHART_COLORS[0]
        },
        label: {
          show: true,
          position: 'top'
        }
      },
      {
        name: '喝水達成率',
        type: 'line',
        yAxisIndex: 1,
        data: achievementRate,
        itemStyle: {
          color: CHART_COLORS[1]
        },
        lineStyle: {
          width: 2
        },
        // 顯示 line 的數值標籤，放在點上方
        label: {
          show: true,
          position: 'top',
          color: CHART_COLORS[1]
        }
      }
    ],
    dataZoom: [
      {
        type: 'slider',
        start: 0,
        end: 100
      },
      {
        type: 'inside',
        start: 0,
        end: 100
      }
    ],
    grid: {
      left: '2%',
      right: '2%',
      bottom: '35%'
    }
  }

  chartInstance.setOption(option)
}

onMounted(() => {
  nextTick(() => {
    initChart()
  })

  window.addEventListener('resize', resizeHandler)
})

watch(() => response.value, () => {
  nextTick(() => {
    initChart()
  })
}, { deep: true })

onUnmounted(() => {
  chartInstance?.dispose()
  window.removeEventListener('resize', resizeHandler)
})
</script>
