<template>
  <SearchBar @search="handleSearch" :limitDays="31">
    <template #icon>
      <v-icon>mdi-water</v-icon>
    </template>
    <template #title>
      營養水分監控
    </template>
    <template #dashboardTypes>
      <select class="select">
        <option>疲勞監控與身體組成分析</option>
      </select>
    </template>

    <template #more>
        <select v-model="trainType" class="select" title="訓練類型">
          <option value="a">專項體能</option>
          <option value="b">一般體能</option>
          <option value="c">重量訓練</option>
          <option value="d">自助練習</option>
        </select>
    </template>
  </SearchBar>

  <section class="pa-4" v-if="response?.datas?.length">
    <v-row>
      <v-col cols="12" md="7">
        <BigNumberContainer />
      </v-col>
      <v-col cols="12" md="5">
        <TrainBar @update:duration="handleDurationUpdate" />
      </v-col>
    </v-row>

    <div class="box bg-white mt-4">
      <h2 class="mb-4 w-75">每日飲水量趨勢</h2>
      <div>
        <WaterChart />
        <WaterTable />
      </div>
    </div>
  </section>

  <NoData v-else :message="queryNoDataText" />
</template>

<script setup>
import SearchBar from '../components/searchBar.vue'
import BigNumberContainer from './components/bigNumberContainer.vue'
import TrainBar from './components/trainBar.vue'
import WaterChart from './components/waterChart.vue'
import WaterTable from './components/waterTable.vue'
import NoData from '../components/noData.vue'
import { dashboard } from '@/requests'

const selectedUserId = ref('')
const response = ref(null)
const 可容許脫水率 = ref(2) // 預設值2%
const 選擇的訓練時間 = ref(60)
const trainType = ref('a')
const queryNoDataText = ref('')

const handleSearch = async (searchParams) => {
  try {
    const queryString = searchParams.queryString + "" +
      `&trainType=${encodeURIComponent(trainType.value)}`
    selectedUserId.value = searchParams.selectedUserId

    const result = await dashboard.getNutritionIndicatorWater(selectedUserId.value, queryString)

    // 處理每筆資料，加上每小時建議飲水量
    if (result.data?.datas?.length) {
      result.data.datas = result.data.datas.map(item => ({
        ...item,
        每小時建議飲水量: (
          item.流汗率 * item.訓練時間 -
          (item.訓練前體重 * 可容許脫水率.value / 100)
        )
      }))
    }

    response.value = result.data
    console.log(1);
    
    if (response.value.datas.length == 0)
      queryNoDataText.value = `${searchParams.selectedUserName}在所選日期範圍內${searchParams.sDate}~${searchParams.eDate}無資料。`
  } catch (error) {
    console.error('API 調用失敗:', error)
  }
}

const handleDurationUpdate = (duration) => {
  選擇的訓練時間.value = duration
}

const 平均流汗率 = computed(() => {
  if (!response.value?.datas?.length) return 0
  const 總流汗率 = response.value.datas.reduce((sum, item) => sum + item.流汗率, 0)
  return (總流汗率 / response.value.datas.length).toFixed(1)
})

// 平均總流失量(用於食鹽量計算)
// 公式: (每筆總流失量/訓練時間的平均值) / 1000 * 選擇的訓練時間
// 先算出每小時平均流失量(L/hr)，再乘以選擇的訓練時間得到總流失量(L)
const 平均總流失量 = computed(() => {
  if (!response.value?.datas?.length) return 0
  const 總和 = response.value.datas.reduce((sum, item) => {
    return sum + (item.總流失量 / item.訓練時間)
  }, 0)
  return 總和 / response.value.datas.length / 1000 * 選擇的訓練時間.value
})

const 平均每小時建議飲水量 = computed(() => {
  if (!response.value?.datas?.length) return 0
  const 總建議飲水量 = response.value.datas.reduce((sum, item) => sum + item.每小時建議飲水量, 0)
  return (總建議飲水量 / response.value.datas.length).toFixed(0)
})

/**這個是大魔王 */
const 建議補充食鹽量 = computed(() => {
  if (!response.value?.局部的汗鈉濃度 || !平均總流失量.value) return 0
  // 公式: (0.57*局部的汗鈉濃度+11.05)*(平均總流失量)*22.99*0.00254
  const 結果 = (0.57 * response.value.局部的汗鈉濃度 + 11.05)
    * 平均總流失量.value * 22.99 * 0.00254
  return 結果.toFixed(1)
})


provide('response', response)
provide('平均流汗率', 平均流汗率)
provide('建議補充食鹽量', 建議補充食鹽量)
provide('可容許脫水率', 可容許脫水率)
provide('平均每小時建議飲水量', 平均每小時建議飲水量)

</script>
